import React, { useEffect, useState } from 'react';
import './Signup.css';
import alertredIcon from '../assets/alert_circle_red.svg';
import alertgrayIcon from '../assets/alert_circle_gray.svg';
import axios from "axios";
import Signup2Form from './Signup2';
import { useNavigate } from 'react-router-dom';

const SignupForm = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [isValidEmail, setIsValidEmail] = useState(null);
  const [isValidPassword, setIsValidPassword] = useState(null);
  const [isPasswordMatch, setIsPasswordMatch] = useState(null);
  const [isSpecialCharIncluded, setIsSpecialCharIncluded] = useState(null);

  const handleScrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' }); //   
  };

  const handleChangeEmail = (e) => {
    const { value } = e.target;
    setEmail(value);
  };

  const handleChangePassword = (e) => {
    const { value } = e.target;
    setPassword(value);
  };

  const handleChangeCheckPassword = (e) => {
    const { value } = e.target;
    setCheckPassword(value);
  };

  const handleBlurEmail = () => {
    //     
    const emailPattern = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i;
    setIsValidEmail(emailPattern.test(email));
  };

  const handleBlurPassword = () => {
    //     
    // const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{11,}$/;
    const passwordPattern = /^(?=.*[0-9])(?=.*[@$!%*?&])[a-zA-Z0-9@$!%*?&]{11,}$/;
    setIsValidPassword(passwordPattern.test(password));
    setIsSpecialCharIncluded(passwordPattern.test(password) && /[@$!%*?&]/.test(password));
  };

  const handleBlurCheckPassword = () => {
    //      
    setIsPasswordMatch(password === checkPassword);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("!");
  };

  const isSubmitDisabled = !isValidEmail || !isValidPassword || !isSpecialCharIncluded || !isPasswordMatch;

  const handleSignupNext = () => {
    //email, password, and checkPassword  Signup2 
    const props = {
      email,
      password,
      checkPassword,
    };
    console.log('Props passed to Signup2:', props); 
    navigate('/signup/next', { state: props })
  };

  // axios.get('/api/hello')
  // .then((Response) => {
  //     console.log(Response); 
  // })
  // .catch((Error) => {
  //     console.log(Error);
  // });

  // const onClickSignupNext = () => {
  //   axios({
  //     url: '/api/user/signup',
  //     method: 'POST',
  //     body: { email, password, passwordCheck, nickName, birthday, gender },
  //   })
  //     .then((res) => {
  //       localStorage.setItem('accessToken', res.data.accessToken);
  //       alert(' .');
  //       window.location.replace('/signUp/next');
  //     })
  //     .catch((err) => {
  //       alert(' ');
  //     });
  // };

  return (
    <div className="signupForm">
      <h2>Festie <br />!</h2>
      <div className="page1">1/2</div>

      <form onSubmit={handleSubmit}>
        <ul>
          <li><strong></strong></li>
          <li></li>
          <input
            type="text"
            className={`emailText ${isValidEmail !== null ? "error" : ""}`}
            placeholder="   "
            onChange={handleChangeEmail}
            onBlur={handleBlurEmail}
          />
          {isValidEmail == false && <span><img src={alertredIcon} className="alertredIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="emailalert">   .</p></span>}
          {isValidEmail !== false && <span><p className="emptyalertform" style={{ marginLeft: '469px', verticalAlign: 'sub' }}></p></span>}
          <li><strong></strong></li>
          <li></li>
          <input
            type="password"
            className={`passwordText ${isValidPassword !== null ? "error" : ""}`}
            placeholder="( )  "
            onChange={handleChangePassword}
            onBlur={handleBlurPassword}
          />        
          {isValidPassword == false && isSpecialCharIncluded == false && <span><img src={alertredIcon} className="alertredIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="pwalert"> .</p></span>}
          {isValidPassword == null && isSpecialCharIncluded == null && <span><img src={alertgrayIcon} className="alertgrayIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="pwform"> /,   11 </p></span>}
          {isValidPassword == true && isSpecialCharIncluded == true && <span><p className="emptyalertform" style={{ marginLeft: '469px', verticalAlign: 'sub' }}></p></span>}
          {/* {isValidPassword !== false && isSpecialCharIncluded == true && <span><p className="emptyalertform" style={{ marginLeft: '469px', verticalAlign: 'sub' }}></p></span>} */}
          <li><strong> </strong></li>
          <li></li>
          <input
            type="password"
            className={`passwordcheckText ${isPasswordMatch !== null? "error" : ""}`}
            placeholder=" "
            onChange={handleChangeCheckPassword}
            onBlur={handleBlurCheckPassword}
          />
          {isPasswordMatch == false && <span><img src={alertredIcon} className="alertredIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="pwcheckalert">  .</p></span>}
          {isPasswordMatch !== false && <span><p className="emptyalertform" style={{ marginLeft: '469px', verticalAlign: 'sub' }}></p></span>}
          {/* <NavLink to="/signUp/next" style={{ textDecoration: "none" }}> */}
            <div
              className={`signupnextBtn ${isSubmitDisabled ? "disabled" : "enabled"}`}
              onClick={(e) => {
                if (isSubmitDisabled) {
                  e.preventDefault(); //   
                  return;
                }
                //      
                handleSignupNext();
              }}
              disabled={isSubmitDisabled}
            >
              
            </div>
          {/* </NavLink> */}
        </ul>
      </form>
    </div>
  );
};

export default SignupForm;

