import React, { useState } from 'react';
import './Resetpw.css';
import alertredIcon from '../assets/alert_circle_red.svg';
import alertgrayIcon from '../assets/alert_circle_gray.svg';
import { NavLink } from "react-router-dom";

const ResetPasswordForm = () => {
  const [password, setPassword] = useState("");
  const [isPwNotFound, setIsPwNotFound] = useState(false);
  const [isSpecialCharIncluded, setIsSpecialCharIncluded] = useState(true);
  const [isValidPassword, setIsValidPassword] = useState(true);

  const handleScrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' }); //   
  };

  const handleChangePassword = (e) => {
    const { value } = e.target;
    setPassword(value);
  };

  const handleBlurPassword = () => {
    //     
    const passwordPattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{11,}$/;
    setIsValidPassword(passwordPattern.test(password));
    setIsSpecialCharIncluded(passwordPattern.test(password) && /[@$!%*?&]/.test(password));
  };

  //   
  const handleBlur = (event) => {
    const inputPw = event.target.value; //  
    const existingPws = ['Festiebestie23!',]; //   

    if (!existingPws.includes(inputPw)) {
      setIsPwNotFound(true);
    } else {
      setIsPwNotFound(false);
    }
  };
  
  return (
    <>
      <div className="resetpwForm">
        <h2> </h2>
        <p className='resetpwExplain'>    </p>

        <form>
          <ul>
            <li><strong> </strong></li>
            {/* <li><input type="password" className="oripwText" placeholder="  " /></li> */}
            <div>
              <input
                type="password"
                className="oripwText"
                placeholder="  "
                onBlur={handleBlur}
              />
              {isPwNotFound && (
                <div>
                  <img
                    src={alertredIcon}
                    className="alertredIcon"
                    style={{ marginLeft: '469px', verticalAlign: 'sub' }}
                  />
                  <p className="resetpworialert">  </p>
                </div>
              )}
            {!isPwNotFound !== false && <span><p className="emptyalertform" style={{ marginLeft: '469px', verticalAlign: 'sub' }}></p></span>}

            </div>
            <li><strong> </strong></li>
            {/* <li><input type="password" className="newpwText" placeholder="  " /></li> */}
            <input
            type="password"
            className={`passwordText ${!isValidPassword ? "error" : ""}`}
            placeholder="  "
            onChange={handleChangePassword}
            onBlur={handleBlurPassword}
            />
            {!isValidPassword && <span><img src={alertredIcon} className="alertredIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="pwalert"> .</p></span>}
            {isValidPassword && isSpecialCharIncluded && <span><img src={alertgrayIcon} className="alertgrayIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} /><p className="pwform"> /,   11 </p></span>}
            {/* <img src={alertgrayIcon} className="alertIcon" style={{ marginLeft: '469px', verticalAlign: 'sub' }} />
            <span className="resetpwnewalert"> /,   11 </span> */}
            <NavLink to="/resetPw/finish" style={{ textDecoration: "none" }}>
              <div className="doneBtn" onClick={handleScrollToTop}></div>
            </NavLink>
          </ul>
        </form>
      </div>

      <div className="togoFind">
        <span className="togofindText">  ?</span>
        <NavLink to="/findPw" style={{ textDecoration: "none" }}>
          <span className="resetfindBtn" onClick={handleScrollToTop}> </span>
        </NavLink>
        <input type="button" className="togoBtn" />
      </div>
    </>
  );
};

export default ResetPasswordForm;
